:root{
    margin: 0;
    padding: 0;
    vertical-align: baseline;
    --randomColor: black;
}
body{
    font-weight: 400;
    font-family: "Raleway",sans-serif;
    background-color: var(--randomColor);
    color: var(--randomColor);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
#quote-box{
    display: flex;
    flex-direction: column;
    width: 400px;
    padding: 20px 30px 20px 30px;
    background-color: white;
    border: 4px;
}
#text{
    font-size: x-large;
    text-align: center;
    padding-bottom: 0;
    margin-bottom: 0;
}
#author{
    font-size: large;
    text-align: right;
    margin-top: 5px;
    margin-bottom: 40px;
    padding-top: 0;
}
#buttons{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
#tweet-quote{
    width: 50px;
    height: 50px;
    display: flex;
    background-color: var(--randomColor);
    color: white;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    text-decoration: none;
}
.fa-twitter:before {
    font-size: 30px;
}
#new-quote{
    font-size: medium;
    padding: 5px 10px 5px 10px;
    color: white;
    background-color: var(--randomColor);
    border-radius: 5px;
}

